/* Add this to your CSS */
.blog-posts-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    /* justify-content: space-between; */
  }
  
  .blog-post {
    width: calc(33.33% - 20px); /* Set to 1/3 of the container minus spacing (adjust the spacing) */
    border: 1px solid #ccc;
    padding: 10px;
    margin-bottom: 20px; /* Add bottom margin to separate blog posts */
  }
  
  .blog-post-image {
    width: 100%; /* Ensure the image fills its container */
    max-height: 200px; /* Set a fixed maximum height for the image */
    object-fit: cover; /* Crop or fit the image within its container */
  }
  
  /* Adjust spacing and dimensions as needed */
  
    
