  @media (max-width: 768px) {
    .blog-post {
      width: calc(50% - 10px); /* Set to 1/2 of the container minus spacing */
    }
  }
  
  /* Media query for screens smaller than 576px (most phones) */
  @media (max-width: 576px) {
    .blog-post {
      width: 100%; /* Set to full width of the container */
    }
    .blog-post-image {
      max-height: 150px; /* Adjust the maximum image height */
    }
  }